import { styled } from '@mui/material/styles';
import {
  Box,
  Typography,
  Divider,
  List,
  ListItem,
  ListItemIcon,
  ListItemText,
} from '@mui/material';
import PlayCircleFilledIcon from '@mui/icons-material/PlayCircleFilled';
import { usePlaylistsPaginatedQuery } from '@/schema';

const PREFIX = 'index';

const classes = {
  root: `${PREFIX}-root`,
  title: `${PREFIX}-title`,
  content: `${PREFIX}-content`,
};

const StyledBox = styled(Box)(({ theme }) => ({
  [`&.${classes.root}`]: {
    backgroundColor: theme.palette.background.paper,
    borderRadius: theme.shape.borderRadius,
  },

  [`& .${classes.title}`]: {
    padding: theme.spacing(1.5, 2),
  },

  [`& .${classes.content}`]: {
    padding: theme.spacing(1.5, 2),
  },
}));

export default function Playlist() {
  const { data, fetchMore } = usePlaylistsPaginatedQuery({
    variables: {
      query: {
        last: 8,
      },
    },
  });
  const loadMore = () => {
    fetchMore({
      variables: {
        query: {
          last: 8,
          before: data?.playlists_paginated?.pageInfo?.endCursor,
        },
      },
      updateQuery: (previousQueryResult, { fetchMoreResult }) => {
        if (fetchMoreResult?.playlists_paginated?.edges) {
          const { edges, pageInfo, totalCount } =
            fetchMoreResult.playlists_paginated;
          return {
            playlists_paginated: {
              pageInfo,
              totalCount,
              edges: [
                ...(previousQueryResult.playlists_paginated.edges || []),
                ...edges,
              ],
              __typename: previousQueryResult.playlists_paginated.__typename,
            },
          };
        }
        return previousQueryResult;
      },
    });
  };

  return (
    <StyledBox className={classes.root}>
      <Typography variant="h6" className={classes.title}>
        Video Manager
      </Typography>
      <Divider />
      <Box className={classes.content}>
        <List>
          <ListItem>
            <ListItemIcon>
              <PlayCircleFilledIcon />
            </ListItemIcon>
            <ListItemText
              primary="Single-line item"
              // secondary={secondary ? 'Secondary text' : null}
            />
          </ListItem>
          <div onClick={loadMore}>更多</div>
        </List>
      </Box>
    </StyledBox>
  );
}
